.ur_account{
    display: flex;
    width: 80%;
    justify-content: space-around;

}
.urAccountLabel{
    font-size: 2.5vw;
}

.account_wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
    height: 70vh;
}

.signOut_button{
    width: 200px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #FAFF00;
    border-radius: 10px;
    &:hover{
        cursor: pointer;
        background-color: #FAFF00;
        color: #000000;
        transition: background-color 0.5s ease; 
        transition: color 0.5s ease; 
    }
}

@media screen and (max-width: 850px) {
    .account_wrapper{
        width: 80%;
        height: 70vh;
    }
    .urAccountLabel{
        font-size: 5vw;
    }
}

@media screen and (max-width: 600px) {
    .urAccountLabel{
        font-size: 7vw;
    }
}