.bracket {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
  .round {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  .match {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  .team {
    margin-right: 20px;
  }

  .tournament-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
  }
  .handAnimation_div{
    display: none;
  }
  
@media screen and (max-width: 994px) {
  .handAnimation_div{
    display: block;
  }
}
  