input {

}

.signIn_wrapper{
    width: 500px;
    margin-left: auto;
    margin-right: auto;
}
.first_form_div{
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.label_input_div{
    width: 90%;
    label {
        display: inline-block;
        width: 200px;
    }
}

.form_button{
    display: flex;
    width: 90%;
    
    span{
        color: #FAFF00;
        cursor: pointer;
        font-size: 16px;
        margin-left: 100px;
    }
    button{
        display: block;
        width: 150px;
        height: 30px;
        background-color: #212121;
        color: #FFFFFF;
        border: 2px solid #FAFF00;
        border-radius: 10px;
        font-weight: bold;
        &:hover {
            background-color: #FAFF00;
            color: #000000;
            transition: background-color 0.5s ease; 
            transition: color 0.5s ease; 
            cursor: pointer;
        }
        
    }
}

@media screen and (max-width: 600px) {
    .label_input_div {
        display: flex;
        flex-direction: column;
    }
    .signIn_wrapper{
        width: 90%;
    }
    .first_form_div {
        h1 {
            font-size: 6vw;
        }
    }
}