nav ul {
    list-style: none;
    display: flex;
    width: 600px;
    justify-content: space-around;
    margin-left: -50vw;


}
nav ul a {
    color: white;
    transition: all 0.2s linear;
    text-decoration: none;
}

nav ul a:hover{
    color: #FAFF00;
    transition: color 0,3s ease;
}


@media screen and (max-width: 1150px){
    nav{
        display: none
    }

}