.about_main {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.sides_about_wrapper {
    display: flex;
    width: 100%;
    justify-content: space-around;
}

.left_about {
    
}

.right_about {
    width: 50%;
    font-family: "Caudex", serif;
    font-weight: 700;
    font-size: 1.5vw;
    color: #FFFFFF;
    i {
        color: #FAFF00;
    }
}

.join_organization {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #FFFFFF;
    width: 20vw;
    height: 45px;
    border: 2px solid #FAFF00;
    border-radius: 10px;
    animation: changeColor 10s infinite;
    font-weight: bold;
    &:hover {
        cursor: pointer;
        background-color: #FAFF00;
        color: #000000;
        transition: background-color 1s ease;
        transition: color 1s ease;
        animation: normal;
    }
    
}

@media screen and (max-width: 800px) {

    .right_about {
        font-size: 3vw;
        width: 60%;
    }
  
    .join_organization {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #FFFFFF;
        font-size: 4vw;
        width: 50vw;
        height: 45px;
        border: 2px solid #FAFF00;
        border-radius: 10px;
        &:hover {
            cursor: pointer;
            background-color: #FAFF00;
            color: #000000;
            transition: background-color 1s ease;
            transition: color 1s ease;
        }
    }

}