.auth_wrapper {
    width: 300px;
    display: flex;
    justify-content: space-around;
}
.auth{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 30px;
    border: 2px solid #FAFF00;
    border-radius: 10px;
    font-weight: bold;

    &:hover {
        cursor: pointer;
    }
}
.signIn{
    color: #FFFFFF;

    &:hover {
        background-color: #FAFF00;
        color: #000000;
        transition: background-color 0.5s ease; 
        transition: color 0.5s ease; 
    }
}
.signUp{
    background-color: #FAFF00;
    color: #000000;

    &:hover {
        background-color: #212121;
        color: #FFFFFF;
        transition: background-color 0.5s ease; 
        transition: color 0.5s ease; 
    }
}

@media screen and (max-width: 460px) {
    .auth{
        width: 70px;
        height: 20px;
        border-radius: 5px;
    }
    .auth_wrapper {
        width: 200px;
        display: flex;
        justify-content: space-around;
    }
}