.tournamentRegistration_main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.team_or_solo_wrapper {
    width: 100%;
    display: flex;
    justify-content: space-around;

}

.team_or_solo {
    width: 30vw;
    height: 30vw;
    background-color: #191919;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    font-size: 2vw;
    color: #FFFFFF;
    font-weight: bold;
    img {
        width: 80%;
    }

    &:hover {
        cursor: pointer;
        transform: scale(1.03);
        transition: transform 1s ease;
    }
}
//_________________________Personal data___________________________________________________________________
.personal_data_wrapper {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.personal_data {
    width: 100%;
    height: 500px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #191919;
    gap: 30px;
}

.personal_data_form {
    display: flex;
    width: 100%;
    justify-content: space-around;
    font-size: 1.3vw;
    color: #FFFFFF;

    .personal_data_form_div{
        display: flex;
        flex-direction: column;
        gap: 30px
    }
    .inputs {
        width: 50%;
        gap: 20px;
        input {
            display: block;
            background-color: #191919;
            color: #FFFFFF;
            border-radius: 5px;
            height: 30px;
        }
    }

}
.personal_data_form_div_phone{
    display: none;
}
.team_data_div_phone{
    display: none;
}
.team_data_wrapper{
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.team_data {
    width: 100%;
    height: 900px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #191919;
    gap: 30px;
}

.labels{
    width: 30%;
}
.team_inputs{
    width: 60%;
    display: flex;
    flex-direction: column;
    gap: 60px;
}
.description {
    position: relative;
    font-size: 12px;
    color: #807F7F;    
}
.team_name_surname {
    display: flex;
    color: #FAFF00;
    font-weight: bold;
    margin-left: 40%
}
.member_line {
    display: flex;
    flex-direction: column;
    gap: 20px;
    input {
        display: inline-block;
        background-color: #191919;
        color: #FFFFFF;
        border-radius: 5px;
        height: 30px;
    }
}

.team_members_names_and_telegram_div{
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 30px;
    border: 2px solid #FFFFFF;
    border-radius: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
}

//__________________Оплата____________________________________________________________________________________________

.payment_wrapper {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.payment {
    width: 100%;
    height: 600px;
    padding-right: 50px;
    padding-left: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #191919;
    gap: 30px;
    box-sizing: border-box;
    ol {
        color: #FFFFFF;
        font-size: 1.5vw;
    }
}
.have_mistrust {
    font-size: 1.2vw;
    color: #807F7F;
}
.payment_title{
    color: #FFFFFF;
    font-size: 2vw;
    font-weight: bold
}

//________________________________________________________________
.lastStep_wrapper {
    width: 60%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lastStep {
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #191919;
    gap: 30px;
    box-sizing: border-box;
    ol {
        color: #FFFFFF;
        font-size: 1.5vw;
    }
}

.continue{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 40px;
    border: 2px solid #FAFF00;
    background-color: #191919;
    color: #FFFFFF;
    font-weight: bold;
    margin-top: 20px;
    border-radius: 5px;
    &:hover{
        cursor: pointer;
        background-color: #FAFF00;
        color: #000000;
        transition: background-color 0.5s ease; 
        transition: color 0.5s ease; 
    }

}



@media screen and (max-width: 1100px) {
    .personal_data_form {
        .personal_data_form_div{
            display: none;
        }
    }
    .personal_data_form_div_phone{
        display: block;
        div{
            display: flex;
            flex-direction: column;
            input{
                display: block;
                background-color: #191919;
                color: #FFFFFF;
                border-radius: 5px;
                height: 30px;
            }
        }
    }
    .team_inputs{
        display: none;
    }
    .labels{
        display: none;
    }
    .team_data_div_phone {
        display: flex;
        width: 100%;
        flex-direction: column;
        align-items: center;
    }
    .member_line {
        display: flex;
        flex-direction: column;
        gap: 41px;
        input {
            width: 20vw;
            height: 20px;

        }
    }
    .team_members_names_and_telegram_div {
        width: 100%;
    }
    .data_span{
        font-size: 20px;
    }
    .team_data, .payment, .lastStep {
        height: auto;

    }
    .payment {
        padding-right: unset;
        padding-left: unset;
    }
    .payment_title{
        font-size: 5vw;
    }
}


@media screen and (max-width: 600px) {
    .team_or_solo_wrapper{
        flex-direction: column;
        justify-content: unset;
        align-items: center;
        gap: 50px;
    }
    .team_or_solo { 
        width: 50vw;
        height: 50vw;
    }
    .data_span{
        font-size: 16px;
    }
    .ol_payment{
        li{
            font-size: 4vw;
        }
        
    }
    .have_mistrust {
        font-size: 3vw;
    }
    .payment_title {
        font-size: 5vw;
    }
    .personal_data_form_div_phone {
        span {
            font-size: 4vw;
        }
    }
}

@media screen and (max-width: 470px){
    .member_line {
        display: flex;
        flex-direction: column;
        gap: 20px;
        input {
            width: 40vw;
            height: 20px;

        }
    }
}