.titles {
    display: flex;
    border: 2px solid #FFFFFF;
    width: 100%;
    height: 40px;
    span{
        display: flex;
        justify-content: center;
        align-items: center;
        border-right: 2px solid #FFFFFF;
    }
}

.date, .time{
    width: 15%
}

.teams {
    width: 40%
}

.status {
    width: 30%
}

.table_grid_wrapper {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
}
.table {
    width: 100%;
}
@media screen and (max-width: 600px) {
    .table_grid_wrapper {
        width: 100%;
    }
    .date, .time, .teams, .status {
        font-size: 12px;
    }
    
}