/* HandAnimation.css */
.hand-animation {
    position: fixed;
    bottom: 20px;
    right: 20px;
    width: 100px;
    height: 100px;
    background: url('../../assets/images/pngwing.com\(5\).png') no-repeat center center;
    background-size: contain;
    opacity: 0;
    transition: opacity 0.5s;
  }
  
  .hand-animation.show {
    opacity: 1;
    animation: swipe 1s ease-in-out infinite;
  }
  
  @keyframes swipe {
    0% { transform: translateX(0); }
    50% { transform: translateX(-20px); }
    100% { transform: translateX(0); }
  }
  