.grid {
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-bottom: 50px;
}
.button{
  width: 30%;
  position: relative;
  top: 100px;
}
.title{
  width: 500px;
  margin-bottom: 100px;
}

.container {
  width: 100%;
}

.card-container {
  display: flex;
  justify-content: center;
  align-items: center;
}
a{
  text-decoration: none;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  margin: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.6s ease;

  &:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  }
}

.card-media {
  height: 100px;
}
.image{
  width: 60%;
  margin-left: 20%;
}
.image_petition{
  margin-left: 30%;
  width: 40%;
}
.tournaments {
  text-align: center;
}

.sport-image {
  width: 80%;
}
.block {
  width: 85%;
  display: flex;
  margin-left: auto;
  margin-right: auto;
  background-color: #212121;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Тінь для підвищення глибини */
  transition: transform 0.3s ease; /* Плавний перехід */
  &:hover {
    transform: translateY(-2px); /* Зміна положення при наведенні миші */
  }
}


.paragraph1 {
  color: #FAFF00;
  font-size: 24px; /* Збільшення розміру шрифту */
  font-weight: bold; /* Жирний шрифт для виділення заголовка */
  margin-bottom: 10px; /* Додатковий простір між абзацами */
}

.paragraph2 {
  color: #FFFFFF;
  font-size: 18px; /* Зменшення розміру шрифту для підтексту */
  line-height: 1.5; /* Збільшення міжрядкового інтервалу для кращої читабельності */
}
.phone {
  display: none;
}

@media screen and (max-width: 1100px) {
  .title{
    width: 100%;
  }
  .card {
    width: 50px;
  }

  .grid{
    display: flex;
    justify-content: center;
    text-align: center;
    height: auto;
  }
  .grid img {
      display: none;
  }
  .card-media {

    height: 100px;
  }
  .image{
    width: 60%;
    margin-left: 20%;
  }
  .button{
    width: 30%;
    position: relative;
    top: 30px;
  }

}

.block_image_logo{
  width: 200px;
}

@media screen and (max-width: 1150px) {
  .button {
    width: "50%";
    left: "auto"
  } 
  .title {
    text-align: center
  }
  .card {
    width: "150px"
  }
}

@media screen and (max-width: 640px) {
  .block_image_logo{
    width: 200px;
    height: 200px;
  }
  .block{
    flex-direction: column;
    .pc{
      display: none;
    }
    .phone{
      display: block;
      display: flex;
    }
  }
}

@media screen and (max-width: 450px){
  .button{
    width: 40%;
  }
}

